import {
  NavigationLocation,
  NavigationRoute,
  VehicleDiagnosticData,
} from '../types/integrated-navigation';
import { USE_MOCK_DATA, API_BASE_URL, mockResponse } from '../config/app-config';

class IntegratedNavigationService {
  private readonly API_BASE_URL = API_BASE_URL;

  //   
  async getCurrentLocation(): Promise<{ latitude: number; longitude: number }> {
    return new Promise((resolve, reject) => {
      navigator.geolocation.getCurrentPosition(
        position => {
          resolve({
            latitude: position.coords.latitude,
            longitude: position.coords.longitude,
          });
        },
        error => {
          reject(error);
        },
        {
          enableHighAccuracy: true,
          timeout: 10000,
          maximumAge: 60000,
        }
      );
    });
  }

  //  
  async searchLocations(
    query: string,
    currentLocation?: { latitude: number; longitude: number }
  ): Promise<NavigationLocation[]> {
    //          
    if (USE_MOCK_DATA) {
      return mockResponse(this.getMockSearchResults(query));
    }

    try {
      const response = await fetch(`${this.API_BASE_URL}/navigation/search`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          query,
          currentLocation,
        }),
      });

      if (!response.ok) {
        throw new Error('  .');
      }

      return await response.json();
    } catch (error) {
      console.error('  :', error);
      //    
      return this.getMockSearchResults(query);
    }
  }

  //   
  async searchNearbyWorkshops(
    location: { latitude: number; longitude: number },
    radius: number = 5000,
    serviceType?: string
  ): Promise<NavigationLocation[]> {
    //          
    if (USE_MOCK_DATA) {
      return mockResponse(this.getMockWorkshops(location));
    }

    try {
      const response = await fetch(`${this.API_BASE_URL}/navigation/workshops/nearby`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          location,
          radius,
          serviceType,
        }),
      });

      if (!response.ok) {
        throw new Error('   .');
      }

      return await response.json();
    } catch (error) {
      console.error('   :', error);
      return this.getMockWorkshops(location);
    }
  }

  //  
  async calculateRoute(
    origin: NavigationLocation,
    destination: NavigationLocation
  ): Promise<NavigationRoute> {
    //          
    if (USE_MOCK_DATA) {
      return mockResponse(this.getMockRoute(origin, destination));
    }

    try {
      const response = await fetch(`${this.API_BASE_URL}/navigation/route`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          origin,
          destination,
        }),
      });

      if (!response.ok) {
        throw new Error('  .');
      }

      return await response.json();
    } catch (error) {
      console.error('  :', error);
      return this.getMockRoute(origin, destination);
    }
  }

  //    
  async getVehicleDiagnostics(vehicleId: string): Promise<VehicleDiagnosticData> {
    //          
    if (USE_MOCK_DATA) {
      return mockResponse(this.getMockDiagnosticData(vehicleId));
    }

    try {
      const response = await fetch(`${this.API_BASE_URL}/vehicles/${vehicleId}/diagnostics`);

      if (!response.ok) {
        throw new Error('    .');
      }

      return await response.json();
    } catch (error) {
      console.error('   :', error);
      return this.getMockDiagnosticData(vehicleId);
    }
  }

  //   
  async getFavoriteLocations(): Promise<NavigationLocation[]> {
    //          
    if (USE_MOCK_DATA) {
      return mockResponse(this.getMockFavoriteLocations());
    }

    try {
      const response = await fetch(`${this.API_BASE_URL}/user/favorites`);

      if (!response.ok) {
        throw new Error('  .');
      }

      return await response.json();
    } catch (error) {
      console.error('  :', error);
      //      
      return this.getMockFavoriteLocations();
    }
  }

  //   
  private getMockFavoriteLocations(): NavigationLocation[] {
    return [
      {
        id: 'fav1',
        name: '',
        address: '   123',
        latitude: 37.5026,
        longitude: 127.0246,
        category: 'general',
      },
      {
        id: 'fav2',
        name: '',
        address: '   456',
        latitude: 37.4969,
        longitude: 127.0378,
        category: 'general',
      },
      {
        id: 'fav3',
        name: ' ',
        address: '   789',
        latitude: 37.5043,
        longitude: 127.0144,
        category: 'workshop',
        rating: 4.8,
        phoneNumber: '02-1234-5678',
      },
    ];
  }

  //  
  async addFavoriteLocation(location: NavigationLocation): Promise<void> {
    //        API    
    if (USE_MOCK_DATA) {
      return mockResponse(undefined);
    }

    try {
      const response = await fetch(`${this.API_BASE_URL}/user/favorites`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(location),
      });

      if (!response.ok) {
        throw new Error('  .');
      }
    } catch (error) {
      console.error('  :', error);
      throw error;
    }
  }

  // Mock  
  private getMockSearchResults(query: string): NavigationLocation[] {
    return [
      {
        id: '1',
        name: `${query}   1`,
        address: '   123',
        latitude: 37.5665,
        longitude: 126.978,
        category: 'general',
        rating: 4.5,
      },
      {
        id: '2',
        name: `${query}   2`,
        address: '   456',
        latitude: 37.5575,
        longitude: 127.0334,
        category: 'general',
        rating: 4.2,
      },
    ];
  }

  private getMockWorkshops(location: {
    latitude: number;
    longitude: number;
  }): NavigationLocation[] {
    return [
      {
        id: 'ws1',
        name: ' ',
        address: '   789',
        latitude: location.latitude + 0.01,
        longitude: location.longitude + 0.01,
        category: 'workshop',
        rating: 4.8,
        distance: 1200,
        isOpen: true,
        phoneNumber: '02-1234-5678',
      },
      {
        id: 'ws2',
        name: '24  ',
        address: '   321',
        latitude: location.latitude - 0.008,
        longitude: location.longitude + 0.015,
        category: 'workshop',
        rating: 4.6,
        distance: 1800,
        isOpen: true,
        phoneNumber: '02-2345-6789',
      },
    ];
  }

  private getMockRoute(
    origin: NavigationLocation,
    destination: NavigationLocation
  ): NavigationRoute {
    const distance =
      Math.sqrt(
        Math.pow(destination.latitude - origin.latitude, 2) +
          Math.pow(destination.longitude - origin.longitude, 2)
      ) * 111000; //   

    return {
      id: 'route1',
      origin,
      destination,
      distance: Math.round(distance),
      duration: Math.round(distance / 30), //  30m/s  
      polyline: 'mock_polyline_data',
      trafficStatus: 'normal',
    };
  }

  private getMockDiagnosticData(vehicleId: string): VehicleDiagnosticData {
    return {
      vehicleId,
      timestamp: new Date(),
      engineStatus: 'good',
      batteryLevel: 85,
      fuelLevel: 65,
      mileage: 12500, //   
      oilPressure: 45,
      coolantTemperature: 90,
      tirePressure: {
        frontLeft: 32,
        frontRight: 31,
        rearLeft: 30,
        rearRight: 32,
      },
      diagnosticCodes: [],
      maintenanceRecommendations: [
        {
          id: 'rec1',
          type: 'oil_change',
          severity: 'medium',
          description: '     .',
          estimatedCost: 80000,
          mileageLimit: 10000,
        },
      ],
    };
  }
}

export default new IntegratedNavigationService();
