//     
export const USE_MOCK_DATA = true;

//   URL 
export const API_BASE_URL = (process.env as { EXPO_PUBLIC_API_URL?: string }).EXPO_PUBLIC_API_URL || 'http://localhost:3000/api';

//      ()
export const API_TIMEOUT = 5000;

//      (ms) -      
export const MOCK_DATA_DELAY = 300;

/**
 *      
 * @param data   
 * @returns       Promise
 */
export function mockResponse<T>(data: T): Promise<T> {
  return new Promise(resolve => {
    setTimeout(() => {
      resolve(data);
    }, MOCK_DATA_DELAY);
  });
}
