import React, {
  createContext,
  useContext,
  PropsWithChildren,
  useEffect,
  useCallback,
  useReducer,
} from 'react';
import * as SecureStore from 'expo-secure-store';
import { Platform } from 'react-native';
import { apiClient } from '@cargoro/api-client';

// Auth Context  
interface AuthContextType {
  signIn: (email: string, password: string) => Promise<void>;
  signUp: (data: SignUpData) => Promise<void>;
  signOut: () => Promise<void>;
  session: string | null;
  user: CustomerUser | null;
  isLoading: boolean;
  error: string | null;
}

interface CustomerUser {
  id: string;
  name: string;
  email: string;
  phone: string;
  profileImage?: string;
  vehicles?: Vehicle[];
  memberSince: string;
  totalServices: number;
  activeBookings: number;
}

interface Vehicle {
  id: string;
  make: string;
  model: string;
  year: number;
  licensePlate: string;
  vin?: string;
  color?: string;
  mileage?: number;
}

interface SignUpData {
  name: string;
  email: string;
  password: string;
  phone: string;
  [key: string]: string; //   
}

// Auth Context 
const AuthContext = createContext<AuthContextType>({
  signIn: async () => {},
  signUp: async () => {},
  signOut: async () => {},
  session: null,
  user: null,
  isLoading: true,
  error: null,
});

// Custom Hook for Auth
export function useSession(): AuthContextType {
  const context = useContext(AuthContext);
  return context;
}

// Async State Hook 
type UseStateHook<T> = [[boolean, T | null], (value: T | null) => void];

// Async State Hook
function useAsyncState<T>(initialValue: [boolean, T | null] = [true, null]): UseStateHook<T> {
  return useReducer(
    (state: [boolean, T | null], action: T | null = null): [boolean, T | null] => [false, action],
    initialValue
  ) as UseStateHook<T>;
}

// Storage Helper Functions
export async function setStorageItemAsync(key: string, value: string | null) {
  if (Platform.OS === 'web') {
    try {
      // Web  localStorage 
      const storage = (globalThis as unknown as Window & { localStorage?: Storage }).localStorage;
      if (storage) {
        if (value === null) {
          storage.removeItem(key);
        } else {
          storage.setItem(key, value);
        }
      }
    } catch (e) {
      console.error('Local storage is unavailable:', e);
    }
  } else {
    if (value == null) {
      await SecureStore.deleteItemAsync(key);
    } else {
      await SecureStore.setItemAsync(key, value);
    }
  }
}

export async function getStorageItemAsync(key: string): Promise<string | null> {
  if (Platform.OS === 'web') {
    try {
      const storage = (globalThis as unknown as Window & { localStorage?: Storage }).localStorage;
      if (storage) {
        return storage.getItem(key);
      }
    } catch (e) {
      console.error('Local storage is unavailable:', e);
    }
  } else {
    return await SecureStore.getItemAsync(key);
  }
  return null;
}

// Storage State Hook
export function useStorageState(key: string): UseStateHook<string> {
  const [state, setState] = useAsyncState<string>();

  //   
  useEffect(() => {
    (async () => {
      const value = await getStorageItemAsync(key);
      setState(value);
    })();
  }, [key]);

  //   
  const setValue = useCallback(
    async (value: string | null) => {
      setState(value);
      await setStorageItemAsync(key, value);
    },
    [key]
  );

  return [state, setValue];
}

// Session Provider Component
export function SessionProvider({ children }: PropsWithChildren) {
  const [[isStorageLoading, session], setSession] = useStorageState('customer-auth-session');
  const [user, setUser] = React.useState<CustomerUser | null>(null);
  const [error, setError] = React.useState<string | null>(null);
  const [isInitialized, setIsInitialized] = React.useState(false); //   
  useEffect(() => {
    const loadUser = async () => {
      try {
        if (session) {
          const userStr = await getStorageItemAsync('customer-user-info');
          if (userStr) {
            setUser(JSON.parse(userStr));
          }
        } else {
          //       ( )
          const guestUser: CustomerUser = {
            id: 'guest',
            name: ' ',
            email: 'guest@example.com',
            phone: '',
            memberSince: new Date().toISOString(),
            totalServices: 0,
            activeBookings: 0,
          };
          setUser(guestUser);
        }
      } catch (error) {
        console.error('Error loading user:', error);
        setUser(null);
      } finally {
        setIsInitialized(true);
      }
    };

    if (!isStorageLoading) {
      loadUser();
    }
  }, [session, isStorageLoading]);

  // API   
  useEffect(() => {
    if (session) {
      apiClient.setAuthToken(session);
    } else {
      apiClient.clearAuthToken();
    }
  }, [session]);

  const signIn = useCallback(
    async (email: string, password: string) => {
      try {
        setError(null);

        // API  
        const response = await apiClient.post<{ token: string; user: CustomerUser }>(
          '/auth/customer/login',
          {
            email,
            password,
          }
        );

        const { token, user } = response;

        //     
        await setSession(token);
        await setStorageItemAsync('customer-user-info', JSON.stringify(user));
        setUser(user);
      } catch (err: unknown) {
        const error = err as { response?: { data?: { message?: string } } };
        console.error('Sign in error:', error);

        //     
        if (__DEV__) {
          console.warn(' :    ');
          const dummyUser: CustomerUser = {
            id: '1',
            name: '',
            email: email,
            phone: '010-1234-5678',
            vehicles: [
              {
                id: 'v1',
                make: '',
                model: '',
                year: 2022,
                licensePlate: '12 3456',
                color: '',
                mileage: 15000,
              },
              {
                id: 'v2',
                make: '',
                model: 'K5',
                year: 2021,
                licensePlate: '34 5678',
                color: '',
                mileage: 32000,
              },
            ],
            memberSince: '2023-01-15',
            totalServices: 12,
            activeBookings: 1,
          };

          await setSession('dev-customer-token-12345');
          await setStorageItemAsync('customer-user-info', JSON.stringify(dummyUser));
          setUser(dummyUser);
          return;
        }

        setError(error.response?.data?.message || ' .');
        throw error;
      }
    },
    [setSession]
  );

  const signUp = useCallback(
    async (data: SignUpData) => {
      try {
        setError(null);

        // API  
        const response = await apiClient.post<{ token: string; user: CustomerUser }>(
          '/auth/customer/signup',
          data
        );

        const { token, user } = response;

        //     
        await setSession(token);
        await setStorageItemAsync('customer-user-info', JSON.stringify(user));
        setUser(user);
      } catch (err: unknown) {
        const error = err as { response?: { data?: { message?: string } } };
        console.error('Sign up error:', error);

        //       
        if (__DEV__) {
          console.warn(' :   ');
          const dummyUser: CustomerUser = {
            id: '1',
            name: data.name,
            email: data.email,
            phone: data.phone,
            vehicles: [],
            memberSince: new Date().toISOString(),
            totalServices: 0,
            activeBookings: 0,
          };

          await setSession('dev-customer-token-12345');
          await setStorageItemAsync('customer-user-info', JSON.stringify(dummyUser));
          setUser(dummyUser);
          return;
        }

        setError(error.response?.data?.message || ' .');
        throw error;
      }
    },
    [setSession]
  );

  const signOut = useCallback(async () => {
    try {
      // API   ()
      if (session) {
        try {
          await apiClient.post('/auth/logout');
        } catch (err) {
          console.warn('Logout API failed:', err);
        }
      }

      //   
      await setSession(null);
      await setStorageItemAsync('customer-user-info', null);
      setUser(null);
    } catch (err) {
      console.error('Sign out error:', err);
      //    
      await setSession(null);
      await setStorageItemAsync('customer-user-info', null);
      setUser(null);
    }
  }, [session, setSession]);

  // isLoading      
  const isLoading = isStorageLoading || !isInitialized;

  return (
    <AuthContext.Provider
      value={{
        signIn,
        signUp,
        signOut,
        session,
        user,
        isLoading,
        error,
      }}
    >
      {children}
    </AuthContext.Provider>
  );
}
